/* section styles */
.navBar {
  background: #ffffff;
  border-radius: 20px;
  padding: 20px;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Main list styles */
.breedsGallery {
  gap: 20px;
  counter-reset: li;
  display: grid;
  grid-auto-rows: 140px;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 20px;
  position: relative;
  min-height: 600px;
}

/* Item styles */
.item {
  border-radius: 20px;
  overflow: hidden;
  transition: all 0.5s;
  position: relative;
}
.item:hover {
  transform: scale(1.07);
}
.item:hover > .selectedBreedText {
  display: flex;
}
.item:nth-child(1) {
  grid-column: auto/span 1;
  grid-row: auto/span 2;
}
.item:nth-child(4) {
  grid-column: auto/span 2;
  grid-row: auto/span 2;
}
.item:nth-child(8) {
  grid-column: auto/span 1;
  grid-row: auto/span 2;
}
.item:nth-child(9) {
  grid-column: auto/span 2;
  grid-row: auto/span 2;
}
.item:nth-child(11) {
  grid-column: auto/span 1;
  grid-row: auto/span 2;
}
.item:nth-child(14) {
  grid-column: auto/span 2;
  grid-row: auto/span 2;
}
.item:nth-child(18) {
  grid-column: auto/span 1;
  grid-row: auto/span 2;
}
.item:nth-child(19) {
  grid-column: auto/span 2;
  grid-row: auto/span 2;
}

.item:hover > .imgWrapper::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    rgba(255, 134, 142, 0.6),
    rgba(255, 134, 142, 0.6)
  );
}

/* Innet item image styles */
.BreedsGalleryImg {
  display: block;
  height: 100%;
  object-fit: cover;
  object-position: right;
  width: 100%;
}
.imgWrapper {
  height: 100%;
}

/* Loader styles */
.loaderWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
}
